require('./socket.io.js')
const socket = io('http://meramind.ru:9090');
const PARTICLE_SIZE = 5;
const UPDATE_RATE = 13;


let canvas = document.createElement('canvas');
let c = canvas.getContext('2d');
let x = 0;
let y = 0;
let clients = {};

document.body.appendChild(canvas);
window.addEventListener('resize', resize);
resize();
step();

function step(event){
	c.fillStyle = "rgba(0, 0, 0, 0.1)";
	c.fillRect(0, 0, canvas.width, canvas.height);
	c.fillStyle = "rgba(255, 255, 255, 1)";

	for (let i in clients){
		if ( clients.hasOwnProperty(i) ){
			let client = clients[i];
			let id = client.id;
			let x = client.x;
			let y = client.y;

			c.beginPath();
			c.arc(client.x, client.y, PARTICLE_SIZE, 0, 2*Math.PI);
			c.fill();
			c.stroke();
		}
	}
	requestAnimationFrame(step);
}


socket.on('connect_error', function(err) {
	console.log('Error connecting to server');
});

socket.on('connect', (connection) => {
	window.addEventListener('mousemove', moved);
	window.addEventListener('touchstart', touchstart);
	window.addEventListener('touchmove', touchmove);

	loop();
});

socket.on('clients', (data) => {
	// console.log(data);
	clients = data;
});

function touchmove(e) {
	let targetTouches = e.targetTouches[0]
	x = targetTouches.clientX;
	y = targetTouches.clientY;
}

function touchstart(e) {

}

function moved(e) {
	x = e.x;
	y = e.y;
}

function loop() {
	socket.emit('event', {
		id: socket.id,
		x,
		y
	})

	setTimeout(loop, UPDATE_RATE);
}

function resize(){
	canvas.width = document.documentElement.clientWidth;
	canvas.height = document.documentElement.clientHeight;
}



